<template>
    <div>
        <svg width="100%" height="400px" style="border: 2px solid black" @mousemove="view_mousemove($event)" @mouseup="view_mouseup" @mouseleave="view_mouseleave" @dblclick="view_dblclick($event)">
            <Edge v-for="e in edges" :key="e.v1.name + e.v2.name" :edge="e" :stroke_color="e.fill" :is_directed="is_directed" @mousemove.native="object_mousemove($event, e)" @dblclick.native="object_dblclick($event, e)" />
            <Vertex v-for="v in vertices" :key="v.name" :vertex="v" :fill="v.fill" @selected="vertex_mousedown" @mousemove.native="object_mousemove($event, v)" @dblclick.native="object_dblclick($event, v)" />
            <svg v-html="graphics.data" :x="graphics.x" :y="graphics.y" style="pointer-events: none"></svg>
        </svg>

        <div>
            <Tool v-for="(cb, i) in tools_cb" :key="i" :create="cb" :obj="{ vertices, edges, graph, graphics }" @activate_tool="activate_tool" />
            <label>Name</label><input type="text" v-model="graphics.code" maxlength="1" style="text-align: center; width: 20px" />
            <button @click="toggle_directed"><span v-if="!is_directed">Set to Directed Graph</span><span v-else>Set to Undirected Graph</span></button>
        </div>

        <br />
        <button @click="dijk_hide = !dijk_hide"><span v-if="!dijk_hide">Hide Dijkstra</span><span v-else>Show Dijkstra</span></button>
        <div v-if="!dijk_hide">
            <div>
                <label>Dijkstra Source</label>
                <select v-model="dijk_source">
                    <option v-for="v in vertices" :key="v.name">{{ v.name }}</option>
                </select>
                <button @click="dijk_hide_costs_matrix = !dijk_hide_costs_matrix"><span v-if="!dijk_hide_costs_matrix">Hide Dijkstra Cost Matrix</span><span v-else>Show Dijkstra Cost Matrix</span></button>
            </div>
            <div v-if="!dijk_hide_costs_matrix">
                <table style="width: 70%; margin-top: 4px">
                    <tr v-for="(row, i) in dijk_costs" :key="i">
                        <th v-for="(value, j) in row" :key="j">{{ value }}</th>
                    </tr>
                </table>
            </div>
            <div>
                <div v-for="(comment, i) in dijk_comments" :key="i">
                    <div>{{ i + 1 }}) {{ comment }}</div>
                    <div>dist = {{ dijk_dists[i] }}</div>
                    <div>pred = {{ dijk_preds[i] }}</div>
                    <br />
                </div>
            </div>
        </div>

        <br />
        <button @click="euler_hide = !euler_hide"><span v-if="!euler_hide">Hide Euler</span><span v-else>Show Euler</span></button>
        <div v-if="!euler_hide">
            <div>
                Has Euler Path? From
                <select v-model="euler_from">
                    <option v-for="v in vertices" :key="v.name">{{ v.name }}</option>
                </select>
                To
                <select v-model="euler_to">
                    <option v-for="v in vertices" :key="v.name">{{ v.name }}</option>
                </select>
                <div>{{ has_euler_path }}</div>
            </div>
            <br />
            <div>Has Euler Circuit?</div>
            <div>{{ has_euler_circuit }}</div>
            <br />
            <div>Euler Path / Circuit: {{ eulers_total }} <button @click="all_euler()">Generate</button></div>
            <div v-if="eulers.length > 0">{{ eulers }}</div>
        </div>

        <br />
        <button @click="ham_hide = !ham_hide"><span v-if="!ham_hide">Hide Hamilton</span><span v-else>Show Hamilton</span></button>
        <div v-if="!ham_hide">
            Hamilton Path: From
            <select v-model="ham_from">
                <option v-for="v in vertices" :key="v.name">{{ v.name }}</option>
            </select>
            To
            <select v-model="ham_to">
                <option v-for="v in vertices" :key="v.name">{{ v.name }}</option>
            </select>
            <div>{{ ham_path }}</div>
        </div>

        <br />
        <button @click="bfs_hide = !bfs_hide"><span v-if="!bfs_hide">Hide BFS</span><span v-else>Show BFS</span></button>
        <div v-if="!bfs_hide">
            BFS: From
            <select v-model="bfs_from">
                <option v-for="v in vertices" :key="v.name">{{ v.name }}</option>
            </select>
            To
            <select v-model="bfs_to">
                <option v-for="v in vertices" :key="v.name">{{ v.name }}</option>
            </select>
            <select v-model="bfs_order">
                <option>Ascending Neighbours</option>
                <option>Descending Neighbours</option>
            </select>
            <div v-html="bfs_path"></div>
            <div v-show="bfs_path.length > 0">
                <table style="width: 70%; margin-top: 4px">
                    <tr>
                        <th>Vertex</th>
                        <th v-for="(column, i) in bfs_predecessor" :key="i">{{ column.vertex }}</th>
                    </tr>
                    <tr>
                        <th>Predecessor</th>
                        <th v-for="(column, i) in bfs_predecessor" :key="i">{{ column.predecessor }}</th>
                    </tr>
                </table>
            </div>
            <div v-if="bfs_steps.length > 0">
                <br />
                <hr />
                <div>Note: Queue = [Head..Tail], Dequeue at Head, Enqueue at Tail</div>
                <br />
                <div v-for="(step, i) in bfs_steps" :key="i">
                    <span>Iteration {{ i + 1 }}</span>
                    <div>Queue: {{ step.queue }}, Visited: {{ step.visited }}</div>
                    <table style="width: 70%; margin-top: 4px">
                        <tr>
                            <th>Vertex</th>
                            <th v-for="(column, i) in step.predecessor" :key="i">{{ column.vertex }}</th>
                        </tr>
                        <tr>
                            <th>Predecessor</th>
                            <th v-for="(column, i) in step.predecessor" :key="i">{{ column.predecessor }}</th>
                        </tr>
                    </table>
                </div>
            </div>
        </div>

        <br />
        <button @click="dfs_hide = !dfs_hide"><span v-if="!dfs_hide">Hide DFS</span><span v-else>Show DFS</span></button>
        <div v-if="!dfs_hide">
            DFS: From
            <select v-model="dfs_from">
                <option v-for="v in vertices" :key="v.name">{{ v.name }}</option>
            </select>
            To
            <select v-model="dfs_to">
                <option v-for="v in vertices" :key="v.name">{{ v.name }}</option>
            </select>
            <select v-model="dfs_order">
                <option>Ascending Neighbours</option>
                <option>Descending Neighbours</option>
            </select>
            <div v-html="dfs_path"></div>
            <div v-show="dfs_path.length > 0">
                <table style="width: 70%; margin-top: 4px">
                    <tr>
                        <th>Vertex</th>
                        <th v-for="(column, i) in dfs_predecessor" :key="i">{{ column.vertex }}</th>
                    </tr>
                    <tr>
                        <th>Predecessor</th>
                        <th v-for="(column, i) in dfs_predecessor" :key="i">{{ column.predecessor }}</th>
                    </tr>
                </table>
            </div>
            <div v-if="dfs_steps.length > 0">
                <br />
                <hr />
                <div>Note: Stack = [Head..Tail], Push at Head, Pop at Head</div>
                <br />
                <div v-for="(step, i) in dfs_steps" :key="i">
                    <span>Iteration {{ i + 1 }}</span>
                    <div>Stack: {{ step.stack }}, Visited: {{ step.visited }}</div>
                    <table style="width: 70%; margin-top: 4px">
                        <tr>
                            <th>Vertex</th>
                            <th v-for="(column, i) in step.predecessor" :key="i">{{ column.vertex }}</th>
                        </tr>
                        <tr>
                            <th>Predecessor</th>
                            <th v-for="(column, i) in step.predecessor" :key="i">{{ column.predecessor }}</th>
                        </tr>
                    </table>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Dijkstra } from "../graph/Dijkstra";
import { graph, vertex, edge } from "../graph/Graph";
import Vertex from "./Vertex";
import Edge from "./Edge";
import Tool from "./Tool";
import { AddVertexTool, RemoveVertexTool, AddEdgeTool } from "./Tools.js";

export default {
    name: "Graph",
    data: function() {
        return {
            vertices: [],
            edges: [],
            graph: new graph(),

            is_directed: false,

            selected: null,
            active_tool: null,
            tools_cb: [AddVertexTool, AddEdgeTool, RemoveVertexTool],
            graphics: { data: null, x: 0, y: 0, code: "0" },

            dijk_hide: true,
            dijk_hide_costs_matrix: true,
            dijk_source: null,
            dijk_costs: null,
            dijk_comments: null,
            dijk_dists: null,
            dijk_preds: null,

            euler_hide: true,
            eulers: [],
            eulers_total: 0,
            has_euler_circuit: "",
            has_euler_path: "",
            euler_from: null,
            euler_to: null,

            ham_hide: true,
            ham_from: null,
            ham_to: null,
            ham_path: "",

            bfs_hide: true,
            bfs_from: null,
            bfs_to: "all",
            bfs_order: "Ascending Neighbours",
            bfs_path: "",
            bfs_predecessor: [],
            bfs_steps: [],

            dfs_hide: true,
            dfs_from: null,
            dfs_to: null,
            dfs_order: "Ascending Neighbours",
            dfs_path: "",
            dfs_predecessor: [],
            dfs_steps: []
        };
    },
    watch: {
        dijk_source: function() {
            this.dijkstra();
        },
        vertices: function(nv) {
            if (nv.length > 0) {
                if (this.dijk_source == null) {
                    this.dijk_source = nv[0].name;
                }
            }
            this.invalidate();
        },
        edges: function() {
            this.invalidate();
        },
        euler_from: function() {
            this.got_euler_path();
        },
        euler_to: function() {
            this.got_euler_path();
        },
        ham_from: function() {
            this.hamilton();
        },
        ham_to: function() {
            this.hamilton();
        },
        bfs_from: function() {
            this.bfs();
        },
        bfs_to: function() {
            this.bfs();
        },
        bfs_order: function() {
            this.bfs();
        },
        dfs_from: function() {
            this.dfs();
        },
        dfs_to: function() {
            this.dfs();
        },
        dfs_order: function() {
            this.dfs();
        }
    },
    created() {},
    methods: {
        toggle_directed: function() {
            let update = false;
            if (this.is_directed) {
                update = confirm("Setting to Undirected Graph. \n!!! Graph will be cleared !!!");
            } else {
                update = confirm("Setting to Directed Graph. \n!!! Graph will be cleared !!!");
            }
            if (update) {
                this.is_directed = !this.is_directed;
                // this.vertices.splice(0, this.vertices.length);
                this.edges.splice(0, this.edges.length);
                // this.graph.vertices = new Map();
                this.graph.remove_all_edges();
                this.graph.is_directed = true;
            }
        },
        vertex_mousedown: function(e) {
            if (this.active_tool) {
                //
            } else {
                this.selected = e;
            }
        },
        activate_tool: function(t) {
            if (this.active_tool) {
                if (this.active_tool == t) return;
                this.active_tool.end();
            }
            if (t.multistep()) {
                this.active_tool = t;
                this.active_tool.enabled = false;
            } else {
                t.apply();
            }
        },
        object_mousemove: function(e, o) {
            e.obj = o;
        },
        object_dblclick: function(e, o) {
            if (this.active_tool) return;
            e.obj = o;
        },
        view_mousemove: function(e) {
            if (this.active_tool) {
                this.active_tool.start();
                this.active_tool.handle(e, e.obj);
            } else {
                if (this.selected) {
                    var rect = e.currentTarget.getBoundingClientRect();
                    this.selected.x = e.clientX - rect.left;
                    this.selected.y = e.clientY - rect.top;
                }
            }
        },
        view_mouseup: function() {
            if (this.active_tool) {
                this.active_tool.apply();
            } else {
                if (this.selected) {
                    this.selected = null;
                }
            }
        },
        view_mouseleave: function() {
            if (this.active_tool) {
                this.active_tool.end();
                this.active_tool.enabled = true;
                this.active_tool = null;
            }
        },
        view_dblclick: function(e) {
            if (e.obj instanceof edge) {
                this.graph.set_edge_weight(e.obj, prompt("Set weight"));
            } else if (e.obj instanceof vertex) {
                let new_name = prompt("Set name");
                if (e.obj.name == this.dijk_source) {
                    this.dijk_source = new_name;
                }
                this.graph.set_vertex_name(e.obj, new_name);
            }
            this.invalidate();
        },
        invalidate: function() {
            this.dijkstra();
            this.auto_all_euler();
            this.euler_circuit();
            this.bfs();
            this.got_euler_path();
        },
        dijkstra: function() {
            if (this.dijk_source) {
                let dijk = new Dijkstra();
                dijk.execute(this.graph, this.dijk_source);
                dijk.print_costs();
                this.dijk_costs = dijk.cost_matrix;
                this.dijk_comments = dijk.comments;
                this.dijk_dists = dijk.dists;
                this.dijk_preds = dijk.preds;
            }
        },
        auto_all_euler: function() {
            if (this.vertices.length <= 7) {
                this.all_euler();
            }
        },
        all_euler: function() {
            this.euler_pathing = [];
            this.eulers = [];
            this.eulers_total = 0;
            this.euler_visited = new Set();
            for (let v1 of this.vertices) {
                for (let v2 of this.vertices) {
                    this.euler_path(v1, v2, this.edges.length);
                }
            }
        },
        euler_path: function(src, dest, d) {
            this.euler_pathing.push(src);
            if (d == 0 && src == dest) {
                let path = this.euler_pathing.map(x => x.name);
                this.eulers.push(path.join("-"));
                this.eulers_total++;
            } else {
                for (let edge of src.edges.values()) {
                    if (!this.euler_visited.has(edge)) {
                        this.euler_visited.add(edge);
                        this.euler_path(edge.get_target(src), dest, d - 1);
                        this.euler_visited.delete(edge);
                    }
                }
            }
            this.euler_pathing.pop();
        },
        got_euler_path: function() {
            let src = this.graph.vertices.get(this.euler_from);
            let dest = this.graph.vertices.get(this.euler_to);
            if (!src || !dest) return;
            if (src != dest) {
                if (src.degree() % 2 == 0 || dest.degree() % 2 == 0) {
                    this.has_euler_path = "No euler path.";
                    if (src.degree() % 2 == 0) {
                        this.has_euler_path += ` Source ${src.name} has even degree.`;
                    }
                    if (dest.degree() % 2 == 0) {
                        this.has_euler_path += ` Destination ${dest.name} has even degree.`;
                    }
                    return false;
                }
            } else if (src.degree() % 2 == 1) {
                this.has_euler_path = `No euler path. Source ${src.name} has odd degree.`;
                return false;
            }
            for (let v of this.vertices) {
                if (v != src && v != dest && v.degree() % 2 == 1) {
                    this.has_euler_path = `No euler path. Vertex ${v.name} has odd degree.`;
                    return false;
                }
            }
            this.has_euler_path = "Yes! There's Euler Path.";
            return true;
        },
        euler_circuit: function() {
            this.has_euler_circuit = "Yes. All vertices are even degree. Check connected yourself.";
            for (let v of this.vertices) {
                if (v.edges.size % 2 == 1) {
                    this.has_euler_circuit = `No. Vertex '${v.name}' has odd degree.`;
                    break;
                }
            }
        },
        hamilton: function() {
            this.ham_visited = new Set();
            this.ham_path_arr = [];
            let src = this.graph.vertices.get(this.ham_from);
            let dest = this.graph.vertices.get(this.ham_to);
            if (!src || !dest) return;
            if (this.find_hamilton(src, dest, this.vertices.length - 1)) {
                this.ham_path = this.ham_path_arr.join("-");
            } else {
                this.ham_path = `No hamilton path found from ${this.ham_from} to ${this.ham_to}`;
            }
        },
        find_hamilton: function(src, dest, d) {
            if (src == dest) {
                if (d == 0) this.ham_path_arr.splice(0, 0, src.name);
                return d == 0;
            }
            this.ham_visited.add(src);
            for (let edge of src.edges.values()) {
                let target = edge.get_target(src);
                if (!this.ham_visited.has(target) && this.find_hamilton(target, dest, d - 1)) {
                    this.ham_path_arr.splice(0, 0, src.name);
                    return true;
                }
            }
            this.ham_visited.delete(src);
            return false;
        },
        bfs: function() {
            let src = this.graph.vertices.get(this.bfs_from);
            let dest = this.graph.vertices.get(this.bfs_to);
            if (!src || !dest) return;
            let found = false;
            let queue = [src];
            let visited = new Set();
            visited.add(src);
            let bfs_pred = new Map();
            for (let v of this.vertices) {
                bfs_pred[v.name] = null;
            }
            bfs_pred[src.name] = src;
            let bfs_steppings = [];
            while (!found && queue.length > 0) {
                bfs_steppings.push({ queue: [...queue.map(x => x.name)], visited: [...Array.from(visited.keys()).map(x => x.name)], predecessor: this.traversal_predecessor(bfs_pred) });
                let v = queue.shift();
                if (v == dest) {
                    found = true;
                    break;
                }
                let neighbours = Array.from(v.edges.values());
                neighbours.sort((a, b) => (a.get_target(v).name > b.get_target(v).name ? 1 : -1));
                if (this.bfs_order == "Descending Neighbours") neighbours.reverse();
                for (let edge of neighbours) {
                    let target = edge.get_target(v);
                    if (!visited.has(target)) {
                        bfs_pred[target.name] = v;
                        visited.add(target);
                        queue.push(target);
                    }
                }
            }

            if (found) {
                this.bfs_path = this.traversal_path(src, dest, bfs_pred);
                this.bfs_predecessor = this.traversal_predecessor(bfs_pred);
                this.bfs_steps = bfs_steppings;
            }
        },
        dfs: function() {
            let src = this.graph.vertices.get(this.dfs_from);
            let dest = this.graph.vertices.get(this.dfs_to);
            if (!src || !dest) return;
            let found = false;
            let stack = [src];
            let visited = new Set();
            let dfs_pred = new Map();
            for (let v of this.vertices) {
                dfs_pred[v.name] = null;
            }
            dfs_pred[src.name] = src;
            let dfs_steppings = [];
            while (!found && stack.length > 0) {
                dfs_steppings.push({ stack: [...stack.map(x => x.name)].reverse(), visited: [...Array.from(visited.keys()).map(x => x.name)], predecessor: this.traversal_predecessor(dfs_pred) });
                let v = stack.pop();
                visited.add(v);
                if (v == dest) {
                    found = true;
                } else {
                    let neighbours = Array.from(v.edges.values());
                    neighbours.sort((a, b) => (a.get_target(v).name > b.get_target(v).name ? 1 : -1));
                    if (this.dfs_order == "Ascending Neighbours") neighbours.reverse();
                    for (let edge of neighbours) {
                        let target = edge.get_target(v);
                        if (!visited.has(target)) {
                            dfs_pred[target.name] = v;
                            stack.push(target);
                        }
                    }
                }
            }

            if (found) {
                this.dfs_path = this.traversal_path(src, dest, dfs_pred);
                this.dfs_predecessor = this.traversal_predecessor(dfs_pred);
                this.dfs_steps = dfs_steppings;
            }
        },
        traversal_path: function(src, dest, pred) {
            let path = [];
            while (dest) {
                path.splice(0, 0, dest.name);
                if (dest == src) break;
                dest = pred[dest.name];
            }
            return `<div>${path.join("-")}</div>`;
        },
        traversal_predecessor: function(pred) {
            let preds = [];
            for (let v of this.graph.get_sorted_vertices_names()) {
                let pre = pred[v];
                pre = pre == null ? "-1" : pre.name;
                preds.push({ vertex: v, predecessor: pre });
            }
            return preds;
        }
    },
    components: {
        Vertex,
        Edge,
        Tool
    }
};
</script>

<style scoped>
table,
th,
td {
    border: 1px solid black;
}
table {
    border-collapse: collapse;
}
button {
    margin: 0px 5px;
}
input {
    margin: 0px 3px;
}
select {
    margin: 0px 3px;
}
</style>