<template>
    <g @mousedown="mousedown">
        <path :stroke="stroke_color" stroke-width="3" :d="edge_path"></path>
        <text font-size="19" :fill="stroke_color" text-anchor="middle" dominant-baseline="central" :x="weight_text_pos[0]" :y="weight_text_pos[1]">{{ this.edge.weight }}</text>
        <g v-if="is_directed">
            <path :stroke="stroke_color" stroke-width="3" :d="edge_arrow_1"></path>
            <path :stroke="stroke_color" stroke-width="3" :d="edge_arrow_2"></path>
        </g>
    </g>
</template>

<script>
export default {
    name: "Edge",
    props: ["edge", "stroke_color", "is_directed"],
    data: function() {
        return {
            radius: 20
        };
    },
    computed: {
        v1: function() {
            return this.edge.v1;
        },
        v2: function() {
            return this.edge.v2;
        },
        degree: function() {
            return Math.atan2(this.v2.y - this.v1.y, this.v2.x - this.v1.x);
        },
        degree_1: function() {
            return this.degree + Math.PI / 2;
        },
        x_offset: function() {
            return this.radius * Math.cos(this.degree);
        },
        y_offset: function() {
            return this.radius * Math.sin(this.degree);
        },
        weight_text_pos: function() {
            let v1x = this.x_offset + this.v1.x;
            let v1y = this.y_offset + this.v1.y;
            let v2x = -this.x_offset + this.v2.x;
            let v2y = -this.y_offset + this.v2.y;
            let cos_perp = Math.cos(this.degree + Math.PI / 2);
            let sin_perp = Math.sin(this.degree + Math.PI / 2);
            return [(v1x + v2x) / 2 + 8 * cos_perp, (v1y + v2y) / 2 + 8 * sin_perp];
        },
        edge_path: function() {
            let v1x = this.x_offset + this.v1.x;
            let v1y = this.y_offset + this.v1.y;
            let v2x = -this.x_offset + this.v2.x;
            let v2y = -this.y_offset + this.v2.y;
            return `M ${v1x} ${v1y} L ${v2x} ${v2y}`;
        },
        edge_arrow_1: function() {
            let v2x = -this.x_offset + this.v2.x;
            let v2y = -this.y_offset + this.v2.y;
            let cos_perp = Math.cos(this.degree + Math.PI / 5.5);
            let a_v2x = v2x + cos_perp * -10;
            let sin_perp = Math.sin(this.degree + Math.PI / 5.5);
            let a_v2y = v2y + sin_perp * -10;
            return `M ${v2x} ${v2y} L ${a_v2x} ${a_v2y}`;
        },
        edge_arrow_2: function() {
            let v2x = -this.x_offset + this.v2.x;
            let v2y = -this.y_offset + this.v2.y;
            let cos_perp = Math.cos(this.degree - Math.PI / 5.5);
            let a_v2x = v2x + cos_perp * -10;
            let sin_perp = Math.sin(this.degree - Math.PI / 5.5);
            let a_v2y = v2y + sin_perp * -10;
            return `M ${v2x} ${v2y} L ${a_v2x} ${a_v2y}`;
        }
    },
    methods: {
        mousedown: function() {
            this.$emit("selected", this.edge);
        }
    }
};
</script>

<style scoped>
g {
    cursor: pointer;
    user-select: none;
}
</style>